import React, { Component } from 'react';
import {Polyline} from 'google-maps-react';

class Historic extends Component {
	constructor(props) {
		super(props);

		this.historicmap = React.createRef();
	}

	componentDidMount = () => {
		this.loadMap();
	}

	componentDidUpdate(prevProps, prevState) {
		//console.log(this.props.currentLocation);
		if (prevProps.google !== this.props.google) {
			this.loadMap();
		}
	}

	// toRad = num => {
	// 	return num * Math.PI/180;
	// }

	// calculateDistance = (lat1, lng1, lat2, lng2) => {
	// 	var dLat = this.toRad(lat2 - lat1);
	// 	var dLng = this.toRad(lng2 - lng1);

	// 	var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) + Math.cos(this.toRad(lat1)) * Math.cos(this.toRad(lat2)) * Math.sin(dLng / 2) * Math.sin(dLng / 2);

	// 	var distance = 6371000 * 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
	// 	return distance;
	// }

	loadMap = () => {
		if (this.props.google) {
			console.log(this.props.favorities);

			var maps = this.props.google.maps;

			const {lat, lng} = this.props.favorities.center; //this.props.currentLocation || 

			//let dist = this.calculateDistance(53.9126229, 27.513717, 53.9078391, 27.5028172)/1000;
			//let dist = this.calculateDistance(this.props.favorities.bounds[1].lat, this.props.favorities.bounds[1].lng, this.props.favorities.bounds[0].lat, this.props.favorities.bounds[0].lng)/1000;
			//console.log(dist);

			//var test = Math.round(14-Math.log(dist/2)/Math.LN2);
			//console.log(test);

			const center = new maps.LatLng(lat, lng);
			const mapConfig = Object.assign({}, {
				center: center,
				zoom: this.props.favorities.zoom,
				zoomControl: false,
    			scaleControl: false,
    			fullscreenControl: false,
    			mapTypeControl: false,
    			streetViewControl: false,
    			// restriction: {
		     //        latLngBounds: {
		     //        	north: this.props.favorities.bounds[1].lat,
		     //        	south: this.props.favorities.bounds[0].lat,
		     //        	east: this.props.favorities.bounds[1].lng,
		     //        	west: this.props.favorities.bounds[0].lng
		     //        },
		     //        strictBounds: true,
		     //    }
			});

			this.map = new maps.Map(this.historicmap.current, mapConfig);


			// km = ( 40000/2 ^ zl ) * 2

			// if (this.map) {
			// 	var strictBounds = new maps.LatLngBounds(
			//     new maps.LatLng(this.props.favorities.bounds[0].lat, this.props.favorities.bounds[0].lng),           // top left corner of map
			//     new maps.LatLng(this.props.favorities.bounds[1].lat, this.props.favorities.bounds[1].lng)            // bottom right corner
			// );
			// this.map.panToBounds(strictBounds);
			// console.log("fired");
			// }
			
		}
	}

	renderChildren = () => {
		const {children} = this.props;

		if (!children) return;

		return React.Children.map(children, c => {
			return React.cloneElement(c, {
				map: this.map,
				google: this.props.google,
				mapCenter: this.props.favorities.center,
			});
		})
	}

	render() {
		if (!this.props.favorities) return;

	  	return (
	  		<div 
    			ref={this.historicmap} 
    			style={{width: this.props.mapDivDimentions.width +"px", height: this.props.mapDivDimentions.height +"px", position: "relative"}} //transformOrigin='center', transform=`translate(-50%,-50%) rotate(${this.props.angle}deg)` //если крутить карту
	          	>
	          	Loading...
	          	{this.renderChildren()}
          	</div>
        );
	}
}

export default Historic;
